.CreateTodoButton {
	background-color: #f6019d;
	box-shadow: 0px 5px 25px rgba(247, 6, 207, 0.8);
	border: none;
	border-radius: 50%;
	cursor: pointer;
	font-size: 50px;
	position: fixed;
	bottom: 24px;
	right: 24px;
	font-weight: bold;
	color: #2de2e6;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 64px;
	height: 64px;

	transform: rotate(0);
	transition: 0.3s ease;
	z-index: 1;
}

.CreateTodoButton:hover {
	transform: rotate(224deg);
}
