.TodoSearch {
	background: rgb(226, 223, 223);
	border-radius: 10px;
	border: 1px solid #d40078;
	margin: 0 24px;
	height: 64px;
	width: calc(100% - 62px);
	font-size: 20px;
	text-align: center;
	color: #540d6e;
	box-shadow: 0px 5px 50px rgba(249, 200, 14, 0.25);
}

.TodoSearch::placeholder {
	color: #2e2157;
	font-size: 20px;
	font-weight: 400;
}

.TodoSearch:focus {
	background: whitesmoke;
	border: 1px solid #d40078;
	outline-color: #f706cf;
	outline-style: solid;
}
