.TodoItem {
	background-color: #2de2e6;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 40px;
	box-shadow: 0px 5px 20px rgba(246, 1, 157, 0.6);
	border-radius: 10px;
	color: #541388;
}

.TodoItem-p {
	margin: 28px 0 24px 24px;
	/* width: calc(100% - 120px); */
	font-size: 20px;
	line-height: 16px;
}
.TodoItem-p--completed {
	text-decoration: line-through;
}

.Icon {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 50px;
	width: 50px;
	font-size: 26px;
	font-weight: bold;
	border-radius: 40%;
	background-color: #2de2e6;
	color: white;
}

.Icon-check {
	position: absolute;
	left: 10px;
}
.Icon-check--active {
	color: #4caf50;
}

.Icon-delete {
	position: absolute;
	top: -24px;
	right: 0;
}
.Icon-delete:hover {
	color: yellow;
}

