.App {
	position: relative;
	min-height: 95vh;
	min-width: 50%;
	text-align: center;
	background-color: #0d0221;
	padding: 10px;
	color: white;
	border-radius: 20px;
}

/* Style the header */
.header {
	background-color: #791e94;
	padding: 20px;
	color: white;
	text-align: center;
	border-top-left-radius: 20px;
	border-top-right-radius: 20px;
}

/* Clear floats after the header */
.header:after {
	content: "";
	display: table;
	clear: both;
}

.tasks {
	background-color: rgba(2, 55, 136, 0.7);
	padding: 20px;
	color: white;
	text-align: center;
	border-bottom-left-radius: 20px;
	border-bottom-right-radius: 20px;
}
